// fullscreen 
// ===== hero ===== 

@if 1 {
  .hero {
    @include adaptiv("padding-top", 165, 90, 1);
    @include adaptiv("padding-bottom", 100, 90, 1);
    background: no-repeat top left;
    background-size: 100%;
  }

  .webp .hero {
    background-image: url(../img/bg-4.webp);
  }

  .no-webp .hero {
    background-image: url(../img/bg-4.jpg);
  }

  .hero__col-item {
    margin-bottom: 15px;
  }

  .hero__title {
    @include adaptiv("margin-bottom", 60, 20, 1);
  }

  .hero__item {
    position: relative;
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: 15px;
    border: 2px solid #FFFFFF;
    box-shadow: 0px 10px 20px rgba(132, 145, 165, 0.1);
    background-image: url(../img/bg-1.svg), linear-gradient(149.64deg, #FFFFFF 10.01%, #FFFFFF 60.77%, rgba(255, 255, 255, 0.2) 91.23%);
    background-repeat: no-repeat;
    background-position: top 5px right 5px, top left;

    &::before {
      position: absolute;
      top: 20px;
      right: 20px;
      font-size: 35px;
      color: $color-hover-1;
    }
  }

  .hero__col {
    display: flex;

    @media (min-width: $xl) {
      display: block;
    }
  }

  .hero__title-3 {
    position: relative;
    padding-bottom: 15px;
    padding-right: 40px;
    margin-bottom: 15px;

    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: -10px;
      display: block;
      width: 230px;
      height: 2px;
      background-color: #F4F5F9;
    }
  }

  .hero__list {
    flex-grow: 1;
    margin-bottom: 15px;

    & li {
      margin-bottom: 5px;

      &::before {
        content: "";
        width: 6px;
        height: 6px;
        display: inline-block;
        margin-right: 10px;
        background: #0A6FA8;
      }
    }
  }

  .hero__link-wrap {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    background-color: #0A6FA8;

    @media (min-width: $xl) {
      margin-top: 140px;
      margin-left: 10px;
      width: 100px;
      height: 100px;
    }

    @media (pointer: fine) {
      &:hover {
        background-color: #4BCAE2;
      }
    }

    &:active {
      background-color: $color-1;
    }
  }

  .hero__link {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    border: 2px solid #FFFFFF;
    transform: translate(-10px, -10px);
    transition: .2s ease 0s;

    &::before {
      font-size: 20px;
      color: #fff;
    }

    @media (min-width: $xl) {
      width: 100px;
      height: 100px;
    }
  }

  .hero__text {
    color: #fff;
    font-style: italic;
    font-weight: 700;
    font-size: 18px;
  }

  @media (min-width: $md) {
    .hero {
      background-size: auto;

      @media (min-width: $xl) {
        margin-bottom: 136px;
      }
    }

    .hero__col-item {
      margin-top: 46px;
    }

    .hero__item {
      padding: 15px;
      background-position: top 10px right 10px, top left;

      @media (min-width: $lg) {
        padding: 30px;
      }
    }

    .hero__text {
      font-size: 22px;
    }
  }
}



// ===== solution ===== 

@if 1 {
  .solution {
    margin-bottom: 100px;
    overflow: hidden;

    @media (min-width: $lg) {
      margin-bottom: 140px;
    }
  }

  .solution__suptitle {
    @media (min-width: $md) {
      text-align: center;
      margin-right: 110px;
    }
  }

  .solution__title {
    margin-bottom: 30px;

    @media (min-width: $md) {
      margin-bottom: 40px;
      text-align: center;
    }
  }


  .tabs {
    position: relative;
    border: 2px solid #fff;

    @media (min-width: $md) {
      border: none;

      &::after {
        content: "";
        position: absolute;
        right: -1000px;
        top: 0;
        display: block;
        height: 100%;
        width: 1000px;
        background-color: #fff;
        background-image: url(../img/bg-solution.png);
        background-repeat: no-repeat;
        background-size: auto 100%;
        background-position: left 200px center;
      }
    }
  }

  .tabs__list {
    @media (min-width: $md) {
      max-width: 330px;
      padding-right: 0;
    }

    @media (min-width: $xl) {
      max-width: 420px;
    }
  }

  .tabs__btn {
    position: relative;
    display: flex;
    justify-content: space-between;
    flex-direction: row-reverse;
    align-items: center;
    width: 100%;
    padding: 15px;
    font-size: rem(18);
    background: linear-gradient(106.57deg, rgba(255, 255, 255, 0.4) 0%, rgba(255, 255, 255, 0.1) 100%);
    backdrop-filter: blur(15px);
    border-bottom: 2px solid #fff;
    transition: .2s ease 0s;

    &--last {
      border-bottom: none;
    }

    &.active {
      color: $color-1;
      background-color: #fff;

      @media (min-width: $md) {
        padding-left: 80px;

        &::after {
          content: "";
          position: absolute;
          left: 20px;
          width: 30px;
          height: 2px;
          background-color: $color-1;

          @media (min-width: $lg) {
            width: 40px;
          }
        }
      }
    }

    &.active::before {
      transform: rotate(180deg);
    }

    @media (pointer: fine) {
      &:hover {
        color: $color-1;
      }
    }
  }


  .spoiler__group {
    padding: 0;
  }

  .tabs__panel {
    height: 0px;
    transition: height 0.5s;
    overflow: hidden;

    @media (min-width: $md) {
      background-color: #fff;
    }

    &.active {
      border-bottom: 2px solid #fff;
    }
  }

  .tabs__desc {
    padding: 15px 15px 0px 15px;
    font-size: rem(16);

    @media (min-width: $xl) {
      display: flex;
      font-size: rem(18);
      padding: 80px 0px 0px 80px;
    }

    & span {
      display: block;
      margin-top: 10px;
      color: #8491A5;
      font-size: rem(15);

      @media (min-width: $xl) {
        font-size: rem(16);
      }
    }

    & picture {
      flex-shrink: 0;
    }

    & img {
      width: 355px;

      @media (min-width: $xl) {
        width: 400px;
        margin-top: -46px;
      }
    }

    & p {
      @media (min-width: $xl) {
        margin-right: 40px;
      }
    }
  }

}



// ===== offer ===== 

@if 1 {
  .offer {
    padding: 40px 0;
    margin-bottom: 100px;
    color: #fff;
    background-image: url(../img/bg-81.png), linear-gradient(259.36deg, #2B9DC5 0%, #0A6FA8 100%);
    background-repeat: no-repeat;
    background-size: 400px auto, 100%;
    background-position: top 325px center, top left;

    @media (min-width: $md) {
      padding: 60px 0;
      background-size: 410px, auto;
      background-position: top 10px left -50px, top left;
    }

    @media (min-width: $xl) {
      margin-bottom: 140px;
      padding: 145px 0 80px 0;
      background-size: 580px, auto;
      background-position: top 10px left -50px, top left;
    }

    @media (min-width: 1900px) {
      padding: 145px 0 80px 0;
      background-size: 640px, auto;
      background-position: top 50px left 250px, top left;
    }
  }

  .offer__suptitle {
    color: #4BCAE2;
  }

  .offer__col-text {
    margin-bottom: 400px;
    font-style: italic;
    font-weight: bold;
    font-size: 18px;

    @media (min-width: $md) {
      font-size: 22px;
      margin: 35px 0px 40px 0px;
    }
  }

  .offer__col {
    @media (min-width: $md) {
      margin-left: auto;
    }
  }

  .offer__title-4 {
    margin-bottom: 20px;
    font-size: 24px;

    @media (min-width: $md) {
      font-size: 30px;
      margin-bottom: 40px;
      text-align: center;
    }
  }

  .offer__item {
    position: relative;
    padding: 15px 50px 15px 15px;
    border: 1px solid #fff;
    background-image: url(../img/bg-2.svg), linear-gradient(105.79deg, rgba(255, 255, 255, 0.5) 0%, rgba(255, 255, 255, 0.1) 100%);
    background-repeat: no-repeat;
    background-position: top 5px right 5px, top left;
    backdrop-filter: blur(15px);

    &::before {
      position: absolute;
      right: 20px;
      top: 20px;
      font-size: 40px;
      color: #4BCAE2;
    }

    @media (min-width: $md) {
      &:first-child {
        margin-left: 33.333333%;
      }
    }

    @media (min-width: $xl) {
      padding: 40px 38px 30px 40px;
    }
  }

  .offer__item-title {
    font-weight: bold;
    font-size: 18px;
    margin-bottom: 10px;

    @media (min-width: $xl) {
      font-size: rem(20);
    }
  }

}




// ===== buns ===== 

@if 1 {
  .buns {
    background: linear-gradient(180deg, #F4F5F9 0%, #FFFFFF 23.44%);
  }

  .buns__that {
    font-style: italic;
    font-weight: bold;
    font-size: 18px;
    margin-bottom: 40px;

    @media (min-width: $md) {
      font-size: 22px;
    }

    @media (min-width: $xl) {
      padding-right: 60px;
    }
  }

  .buns__col-list {
    margin-bottom: 40px;
  }

  .buns__list {
    color: #8491A5;
    counter-reset: list;
    margin-bottom: 40px;

    & li {
      position: relative;
      margin-bottom: 10px;
      padding-left: 53px;

      &::before {
        counter-increment: list;
        content: "- 0."counter(list);
        position: absolute;
        left: 0;
        font-weight: bold;
        font-size: 18px;
        color: $color-1;

        @media (min-width: $md) {
          font-size: 20px;
        }
      }
    }
  }

  .buns__col-img {
    margin-bottom: 40px;

    @media (min-width: $md) {
      margin-top: -250px;
    }

    @media (min-width: $xl) {
      margin-top: -110px;
    }

    & img {
      @media (min-width: $md) {
        max-width: 370px;
      }

      @media (min-width: $xl) {
        max-width: 100%;
      }

    }
  }

  .buns__bg {
    position: relative;
    margin-bottom: 50px;
    transform: translate(10px, 10px);
    background: #F4F5F9;
    padding: 10px 15px 30px 5px;
    font-size: 16px;

    @media (min-width: $md) {
      padding: 20px 15px 63px 5px;
      margin-bottom: 80px;
      font-size: 18px;
    }

    &::before,
    &::after {
      content: "";
      position: absolute;
      left: -10px;
      display: block;
      width: 100%;
      height: 2px;
      background-color: $color-hover-1;
    }

    &::before {
      top: -10px;

      @media (min-width: $md) {
        top: -20px;
      }
    }

    &::after {
      bottom: 10px;

      @media (min-width: $md) {
        bottom: 20px;
      }
    }
  }

  .buns__bg-text {
    max-width: 820px;
    margin: 0 auto;
    text-align: center;
  }

  .buns__item {
    position: relative;
    padding: 0px 0px 0px 110px;
    margin-bottom: 40px;
    background-image: url(../img/bg-3.svg);
    background-repeat: no-repeat;
    font-size: rem(16);

    &::before {
      position: absolute;
      top: 30px;
      left: 30px;
      font-size: 40px;
      color: $color-hover-1;
    }

    @media (min-width: $md) {
      padding: 96px 0px 0px 0px;
      font-size: rem(18);
    }

    @media (min-width: $xl) {
      margin-bottom: 40px;
    }
  }

  .buns__reg {
    position: relative;
    z-index: 1;
    padding: 20px 30px 30px 30px;
    color: #fff;
    text-align: center;
    background-image: url(../img/bg-9.jpg);
    background-size: 100% 100%;
    box-shadow: 0px 35px 70px -10px rgba(10, 111, 168, 0.3);
    transform: translateY(50%);

    @media (min-width: $xl) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 40px 140px 40px 140px;
      text-align: left;
    }
  }

  .buns__reg-title {
    margin-bottom: 10px;
    font-size: rem(28);
    line-height: 120%;

    @media (min-width: $xl) {
      font-size: rem(36);
    }
  }

  .buns__reg-text {
    margin-bottom: 20px;
    font-size: rem(18);
    font-weight: 700;

    @media (min-width: $xl) {
      font-size: rem(20);
    }
  }

  .buns__btn {
    width: 100%;
    max-width: 270px;
    background-color: #fff;
    transition: .1s ease 0s;

    @media (pointer: fine) {
      &:hover {
        color: #fff;
        background-color: $color-hover-1;
        border-color: #fff;
      }
    }
  }
}



// ===== fast ===== 

@if 1 {
  .fast {
    margin-bottom: 100px;

    @media (min-width: $md) {
      display: flex;
      margin-bottom: 140px;
    }

    @media (min-width: $xl) {
      margin-bottom: 180px;
      padding: 40px 0px 0px 0px;
    }
  }


  .marquee {
    overflow: hidden;
    height: 100px;
    margin-top: 95px;
    @include adaptiv("font-size", 100, 36, 1);
    width: 100%;
    color: #fff;
    letter-spacing: 0.03em;
    text-transform: uppercase;
    font-weight: 700;
    white-space: nowrap;

    @media (min-width: $md) {
      margin-top: 70px;
      height: 140px;
    }

    @media (min-width: $xl) {
      margin-top: 50px;
      height: 140px;
    }
  }

  .marquee p {
    padding-left: 100%;
    animation: marquee 15s linear infinite;

    @media (min-width: $xl) {
      animation: marquee 30s linear infinite;
    }
  }

  .marquee p:hover {
    animation-play-state: paused;
  }

  @keyframes marquee {
    0% {
      transform: translate(0, 0);
    }

    100% {
      transform: translate(-200%, 0);
    }
  }




  .fast-left__container {
    padding: 0px 10px;
  }

  .fast__right {
    padding-top: 145px;
  }

  .fast-right__container {
    padding: 0 15px;
  }

  .fast__title {
    margin-bottom: 20px;
    font-size: 24px;

    @media (min-width: $md) {
      font-size: 30px;
    }
  }

  .fast__text {
    font-size: 16px;

    @media (min-width: $md) {
      font-size: 18px;
    }
  }

  .fast__btn {
    margin-top: 10px;
    width: 100%;

    @media (min-width: $md) {
      width: auto;
      margin-top: 20px;
    }
  }

  @media (min-width: $md) {
    .fast__left {
      flex: 1 1 635px;
    }

    .fast-left__container {
      max-width: 635px;
      padding-right: 20px;
      margin-left: auto;
    }

    .fast__right {
      flex: 1 1 635px;
      padding-top: 15px;
    }

    .fast-right__container {
      max-width: 635px;
      // padding-left: 90px;
    }
  }

  @media (min-width: $lg) {
    .fast-right__container {
      padding-left: 27%;
    }

    .fast__bg-container {
      padding-right: 30%;
    }
  }

  .fast__bg {
    position: relative;
    padding: 40px 0px 175px;
    font-style: italic;
    font-weight: bold;
    font-size: 18px;
    color: #fff;
    background-image: url(../img/bg-11.jpg);
    background-repeat: no-repeat;
    background-size: 100% 100%;

    & img {
      position: absolute;
      left: 50%;
      width: 200px;
      transform: translateX(-50%);
    }

    @media (min-width: $lg) {
      & img {
        top: -100px;
        right: 0px;
        left: auto;
        width: 350px;
        transform: translateX(50%);
      }
    }

    @media (min-width: $xl) {
      font-size: rem(22);

      & img {
        width: 420px;
      }
    }
  }


}



// ===== media ===== 

@if 1 {
  .media {
    // margin: 0px 0px 100px 0px;
    // background-color: #F4F5F9;
  }

  .media__row-title {
    display: flex;
    align-items: center;
    margin-bottom: 40px;

    &--right {
      flex-direction: row-reverse;
    }

  }

  .media__suptitle {
    display: flex;
    align-items: center;
    margin-top: 11px;

    &--left::after {
      content: "";
      display: block;
      margin: 0 10px;
      width: 40px;
      height: 2px;
      background-color: $color-1;

      @media (min-width: $md) {
        margin: 0 20px;
      }
    }

    &--right::before {
      content: "";
      display: block;
      margin: 0 10px;
      width: 40px;
      height: 2px;
      background-color: $color-1;

      @media (min-width: $md) {
        margin: 0 20px;
      }
    }

  }

  .media__title {
    margin-bottom: 0;
  }

  .bedia__list {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 100px;

    & img {
      filter: grayscale(1);
    }

    & li {
      flex: 0 0 33.333333%;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 6px 24px;
      height: 84px;
      background: linear-gradient(106.57deg, rgba(255, 255, 255, 0.4) 0%, rgba(255, 255, 255, 0.1) 100%);
      backdrop-filter: blur(15px);
      transition: .2s ease 0s;

      @media (min-width: $md) {
        height: 140px;
      }

      @media (pointer: fine) {
        &:hover {
          background-color: #fff;

          & img {
            filter: grayscale(0);
          }
        }

      }
    }

  }
}



// ===== contact ===== 

@if 1 {
  .contact {
    margin: 0px 0px 100px 0px;
  }

  .contact__left {
    // background-color: #f7f7f7;
  }

  .contact-left__container {
