      padding-top: 80px;
      margin-left: auto;
    }

    .contact__right {
      flex: 1 1 880px;
      padding: 80px 0px 80px 0px;
      font-size: 18px;
    }

    .contact-right__container {
      max-width: 880px;
      padding-left: 40px;
    }
  }

  .contact__label {
    font-weight: bold;
    font-size: 12px;
    line-height: 180%;
    color: #4BCAE2;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    margin-bottom: 10px;

    @media (min-width: $md) {
      font-size: 14px;
    }
  }

  .map {
    width: 100%;
    height: 220px;
  }

  .contact__col {
    margin-bottom: 40px;

    @media (min-width: $md) {
      margin-bottom: 20px;
    }
  }

  .contact__desc {

    @media (min-width: $md) {}
  }

  .contact__title-3 {
    padding-top: 40px;
    margin-bottom: 20px;
    font-size: 24px;
    line-height: 140%;
    color: #262F3C;

    @media (min-width: $lg) {
      font-size: 30px;
      margin-bottom: 0;
      padding: 40px 88px 40px 10px;
      background: linear-gradient(106.27deg, rgba(255, 255, 255, 0.5) 0%, rgba(255, 255, 255, 0.1) 100%);
      border-bottom: 2px solid #FFFFFF;

    }
  }

  .contact__text {
    padding-bottom: 40px;

    @media (min-width: $lg) {
      padding: 40px 60px 40px 10px;
    }
  }

  .contact__row-form {
    color: $color-gray;
    border: 1px solid #fff;

    @media (min-width: $md) {
      display: flex;
    }
  }

  .contact__col-info {
    @media (min-width: $lg) {
      flex: 0 0 30.5%;
      border: 1px solid #fff;
    }
  }

  .contact__col-form {
    @media (min-width: $lg) {
      flex: 0 0 69.5%;
      border: 1px solid #fff;
    }
  }

  .contact__form {
    @media (min-width: $md) {
      padding: 22px 20px 20px 20px;
    }

    @media (min-width: $lg) {
      padding: 52px 40px 40px 40px;
    }
  }

  .form__btn {
    @media (min-width: $md) {
      flex: 0 0 auto;
      margin-left: auto;
    }
  }

  .contact__list a {
    transition: .1s ease 0s;

    @media (pointer: fine) {
      &:hover {
        text-decoration: underline;
      }
    }
  }

  .contact__icon {
    position: relative;
    padding-left: 30px;
    margin-bottom: 10px;

    &::before {
      position: absolute;
      left: 0;
      top: 6px;
      margin-right: 10px;
      font-size: 20px;
      color: #4BCAE2;
    }
  }
}



// ===== page ===== 

@if 1 {
  .wallet {
    .webp & {
      background-image: url(../img/bg-wallet.webp);
    }

    .no-webp & {
      background-image: url(../img/bg-wallet.jpg);
    }

    @media (min-width: $md) {
      background-size: 100% 560px;

      .webp & {
        background-image: none;
      }

      .no-webp & {
        background-image: none;
      }
    }
  }

  .cabinet {
    .webp & {
      background-image: url(../img/bg-cabinet.webp);
    }

    .no-webp & {
      background-image: url(../img/bg-cabinet.jpg);
    }

    @media (min-width: $md) {
      background-size: 100% 560px;

      .webp & {
        background-image: none;
      }

      .no-webp & {
        background-image: none;
      }
    }
  }

  .invest {
    .webp & {
      background-image: url(../img/bg-invest.webp);
    }

    .no-webp & {
      background-image: url(../img/bg-invest.jpg);
    }

    @media (min-width: $md) {
      background-size: 100% 560px;

      .webp & {
        background-image: none;
      }

      .no-webp & {
        background-image: none;
      }
    }
  }

  .page {
    padding: 90px 0px 20px 0px;
    background: no-repeat top left;
    background-size: 100% 412px;
    background-color: #F4F5F9;

    @media (min-width: $md) {
      padding-bottom: 100px;
      background-size: 100% 500px;

      .webp & {
        background-image: url(../img/bg-page.webp);
      }

      .no-webp & {
        background-image: url(../img/bg-page.jpg);
      }
    }

    @media (min-width: $lg) {
      background-size: 87% 500px;
    }

    @media (min-width: $xl) {
      min-height: 810px;
      padding: 120px 0px 60px 0px;
      background-size: auto 100%;
      background-position: top left -30px;
    }
  }

  .page__suptitle {
    color: #4BCAE2;

    &::before {
      position: relative;
      top: -1px;
      margin-right: 16px;
      font-size: 24px;
      transition: .1s ease 0s;
    }

    @media (pointer: fine) {
      &:hover {
        color: #466ace;
      }
    }
  }

  .detail {
    padding-bottom: 90px;

    @media (min-width: $xl) {
      background-size: auto;
      background-attachment: fixed;
    }
  }

  .detail__row {
    align-items: start;
    margin-left: 0;
  }

  .detail__col {
    @media (min-width: $xl) {
      position: sticky;
      top: 120px;
    }
  }


  .page__col-img {
    background-repeat: no-repeat;
    background-size: auto 100%;
    background-position: center right 30px;
  }

  @media (min-width: $lg) {
    .wallet-bg {
      background-image: url(../img/bg-wallet1.png);
    }

    .cabinet-bg {
      background-image: url(../img/bg-cabinet1.png);
    }

    .invest-bg {
      background-image: url(../img/bg-invest1.png);
    }
  }

  .page__list-title {
    order: 1;
    font-style: italic;
    font-weight: bold;
    font-size: 18px;
    margin-bottom: 20px;

    @media (min-width: $md) {
      font-size: 22px;
      order: 0;
    }
  }

  .page__col-list {}

  .page__list {
    order: 1;
    margin-bottom: 60px;

    @media (min-width: $md) {
      order: 0;
    }
  }

  .page__row-btn {
    position: fixed;
    bottom: 0;
    left: 0;
    display: flex;
    flex-direction: row-reverse;
    width: 100%;
    padding: 10px 15px 10px 15px;
    margin: 0;
    background-color: #fff;

    @media (min-width: $md) {
      position: static;
      flex-direction: row;
      padding: 0;
      background-color: transparent;
    }
  }

  .page__btn {
    width: 100%;
    padding: 5px 20px;

    @media (min-width: $sm) {
      padding: 5px 30px;
    }

    @media (min-width: $md) {
      flex: 0 0 auto;
    }
  }

  .card-row {
    order: 1;

    @media (min-width: $md) {
      order: 0;
    }
  }



  .card {
    position: relative;
    // display: flex;
    // flex-direction: column;
    height: 100%;
    padding: 15px 50px 15px 15px;
    border: 1px solid #fff;
    background-image: url(../img/bg-21.svg), linear-gradient(105.79deg, rgba(255, 255, 255, 0.5) 0%, rgba(255, 255, 255, 0.1) 100%);
    background-repeat: no-repeat;
    background-position: top 5px right 5px, top left;
    backdrop-filter: blur(15px);

    &::before {
      position: absolute;
      right: 20px;
      top: 20px;
      font-size: 40px;
      color: $color-hover-1;
    }

    @media (min-width: $md) {}

    @media (min-width: $xl) {
      padding: 40px 38px 30px 40px;
    }
  }


  .card__title {
    // flex-grow: 1;
    font-weight: bold;
    font-size: 18px;
    margin-bottom: 10px;
    padding-right: 20px;

    @media (min-width: $md) {
      font-size: 20px;
    }
  }

  .card__text {
    margin-bottom: 9px;
    color: $color-gray;
  }

  .more__btn {
    color: $color-1;
    transition: .1s ease 0s;

    @media (min-width: $xl) {
      display: none;
    }

    @media (pointer: fine) {
      &:hover {
        color: $color-hover-1;
      }
    }
  }

  .detail__col {
    margin: 0 -10px;
  }

  .detail__col-card {
    @media (min-width: $xl) {
      flex: 0 0 33.333333% !important;
      width: 33.333333% !important;
    }
  }

  .detail-col {
    @media (min-width: $md) {
      flex: 0 0 50% !important;
      width: 50% !important;
    }

    @media (min-width: $xl) {
      flex: 0 0 100% !important;
      width: 100% !important;
    }
  }
}