// ===== Preloader ===== 

@if 1 {
  .preloader {
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    z-index: 1000;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    background: #ffffff;
    transition: .5s;

    &.loaded {
      opacity: 0;
      visibility: hidden;
    }
  }

  .preloader-body {
    text-align: center;
  }

  .loading-center-object {
    width: 200px;
    height: 200px;
    transform: rotate(-135deg)
  }

  .object {
    border-radius: 50%;
    position: absolute;
    border: 4px solid #2b9dc5;
    border-bottom-color: transparent;
    border-right-color: transparent;
    animation: animate 2s infinite;

    .preloader.loaded & {
      animation: none;
    }
  }

  .object--one {
    left: 75px;
    top: 75px;
    width: 50px;
    height: 50px;
  }

  .object--two {
    top: 65px;
    left: 65px;
    width: 70px;
    height: 70px;
    animation-delay: 0.2s;
  }

  .object--three {
    top: 55px;
    left: 55px;
    width: 90px;
    height: 90px;
    animation-delay: 0.4s;
  }

  .object--four {
    top: 45px;
    left: 45px;
    width: 110px;
    height: 110px;
    animation-delay: 0.6s;
  }

  @keyframes animate {
    50% {
      transform: rotate(360deg) scale(0.8);
    }
  }
}