// ===== btn ===== 

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-height: rem(50);
  padding: 5px 30px;
  font-size: rem(12);
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  transition: .2s;

  &::before {
    margin-right: 10px;
  }

  @media (min-width: $md) {
    min-height: rem(50);
  }

  & .icon {
    flex-shrink: 0;
  }
}


.btn-1 {
  color: $color-1;
  border: 2px solid $color-1;

  @media (pointer: fine) {
    &:hover {
      color: $color-hover-1;
      border-color: $color-hover-1;
    }
  }

  &:active {
    color: $color-active-1;
    border-color: $color-active-1;
  }
}


.btn-2 {
  background-color: $color-1;
  color: #ffffff;
  box-shadow: 0px 15px 30px rgba(10, 111, 168, 0.2);

  @media (pointer: fine) {
    &:hover {
      background-color: $color-hover-1;
    }
  }

  &:active {
    background-color: $color-active-1;
  }
}