// ===== head js px ===== 

@if 1 {
  .head {
    position: fixed;
    z-index: 2;
    left: 0;
    top: 0;
    width: 100%;
    transition: background-color .3s ease 0s;

  }


  .head {
    height: 60px;
  }

  .overlay,
  .navbar {
    position: fixed;
    top: 0px;
  }

  .overlay {
    left: 0px;
  }

  .navbar {
    right: 0px;
    height: calc(100% - 0px);
  }

  @media (min-width: $lg) {
    .head {
      height: 90px;
    }

    .head.scroll {ч
      .lang {
        flex-grow: 1;
      }
    }
  }


  //=== scroll ===

  .head.scroll {
    background-color: #F4F5F9;

  }


  .head__body {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    padding: 0 5px;

    @media (min-width: $md) {
      padding: 0 10px;
    }
  }


  .logo {
    flex-grow: 1;
    flex-shrink: 0;
    position: relative;
    display: flex;
    align-items: center;
    margin-right: 20px;
  }

  .logo__img {
    width: 129px;
    height: 24px;

    @media (min-width: $lg) {

      width: 162px;
      height: 30px;
    }

  }

  .logo__img--b {
    display: none;

    .head.scroll & {
      display: inline-block;
    }
  }

  .logo__img--w {

    .head.scroll & {
      display: none;
    }
  }

  .logo__link {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }



  .head__btn {
    display: none;

    @media (min-width: $sm) {}

    @media (min-width: $lg) {
      display: inline-flex;
      min-height: rem(50);
    }
  }

}




// ===== menu-language ===== 

@if 1 {
  .lang {
    display: none;
    margin: 0px 40px 0px 40px;

    @media (min-width: $lg) {
      display: block;
    }
  }

  .lang__body {
    position: relative;
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }

  .lang__btn {
    display: flex;
    align-items: center;
    transition: .1s ease 0s;

    @media (pointer: fine) {
      &:hover {
        color: $color-1;
      }
    }
  }

  .lang__arrow {
    margin-left: 10px;
    transition: .1s ease 0s;
  }

  .lang__btn.active .lang__arrow {
    transform: rotate(180deg);
  }

  .lang__list {
    opacity: 0;
    visibility: hidden;
    transition: .2s ease 0s;
    position: absolute;
    top: 100%;
    right: -35px;
    padding: 10px 0px 0px 0px;
    background-color: #F4F5F9;

    &.show {
      opacity: 1;
      visibility: visible;
    }

    & li {
      margin-bottom: 2px;

    }

    & a {
      display: block;
      padding: 10px 30px 10px 30px;
      background-color: #fff;
      text-align: center;
      border: 2px solid transparent;


      &.active {
        background-color: #F4F5F9;
        border-color: #fff;
        color: $color-1;
      }

      @media (pointer: fine) {
        &:hover {
          color: $color-1;
          background-color: #F4F5F9;
          border-color: #fff;
        }
      }
    }
  }

  @media (pointer: fine) {
    .lang:hover .lang__list {
      opacity: 1;
      visibility: visible;
    }

    .lang:hover .lang__arrow {
      transform: rotate(180deg);
    }
  }
}




//  menu   

@if 1 {
  .overlay {
    z-index: 3;
    right: 0;
    bottom: 0;
    display: none;
    background-color: rgba(#0A6FA8, .75);

    &.active {
      display: block;

      @media (min-width: $xl) {
        display: none;
      }
    }
  }


  .navbar {
    z-index: 3;
    width: 100%;
    max-width: 290px;
    overflow: auto;
    transition: .2s ease 0s;
    transform: translateX(100%);

    &.active {
      transform: translateX(0);
    }
  }

  .btn-close {
    position: absolute;
    top: 7px;
    right: 20px;
    font-size: 30px;
    color: #64748B;
  }

  .navbar__body {
    display: flex;
    flex-direction: column;
    min-height: 100%;
    width: 100%;
    padding: 0px 0px 0px 0px;
    font-size: rem(16);
    // font-weight: 700;
    color: #262F3C;
    line-height: 160%;
    // text-align: center;
  }

  .navbar__top {
    flex-grow: 1;
    padding: 20px 15px 20px 15px;
    background-color: #F4F5F9;

  }

  .navbar__btn {
    width: 100%;
  }

  .navbar__bottom {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    padding: 40px 20px 20px 20px;
    background-color: #fff;

  }

  .navbar__lang {
    display: flex;
    border: 2px solid #fff;
    margin: 36px 0px 30px 0px;

    & li {
      flex-grow: 1;

      &:not(:last-child) {
        border-right: 2px solid #fff;
      }
    }

    & a {
      display: block;
      padding: 10px 10px 10px 10px;
      text-align: center;

      &.active {
        background-color: #fff;
        color: $color-1;
        // background-color: #F4F5F9;
        // border: 2px solid #fff;
      }
    }
  }

  .navbar__link {
    flex-grow: 1;
    padding: 0 15px;

    & li {
      margin-bottom: 20px;
    }
  }

  .menu-start,
  .head__tel-start {
    flex-grow: 1;
  }


  .menu {
    font-size: 16px;
    padding: 0 20px;
    // font-weight: 700;

    & li {
      margin-bottom: 5px;
    }

    @media (min-width: $lg) {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      padding: 0px 0px 0px 0px;
      margin-left: 40px;

      // color: #fff;
      & li {
        margin-bottom: 0px;
      }

      & li:not(:last-child) {
        // margin-right: 10px;
      }
    }
  }

  .menu a {
    display: inline-block;
    width: 100%;
    padding: 10px 0;
    transition: .2s;

    @media (pointer: fine) {
      &:hover {
        color: $color-1;
      }
    }

    &:active {
      color: $color-active-1;
    }
  }

}




// ===== burger ====== 

@if 1 {
  .wrap__burger {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    margin-left: 20px;
    background-color: #2e9ccc;

    .head.scroll & {
      background-color: transparent;
    }
  }

  .burger {
    position: relative;
    z-index: 4;
    display: inline-block;
    width: 18px;
    height: 16px;
  }

  @media (min-width: $lg) {
    .wrap__burger {
      display: none;
    }

    .burger {
      display: none;
    }
  }

  .burger::before,
  .burger::after {
    content: '';
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #fff;
    transition: .2s ease 0s;

    .head.scroll & {
      background-color: #64748B;
    }
  }

  .burger::before {
    top: 0;
  }

  .burger::after {
    bottom: 0;
  }

  .burger.active::before {
    top: 7px;
    transform: rotate(45deg);
    background: #64748B;
  }

  .burger.active::after {
    bottom: 7px;
    transform: rotate(-45deg);
    background: #64748B;
  }

  .burger span {
    position: absolute;
    left: 0;
    width: 100%;
    height: 2px;
    top: 7px;
    background-color: #fff;
    transition: .2s ease 0s;

    .head.scroll & {
      background-color: #64748B;
    }
  }

  .burger.active span {
    transform: scale(0);
  }
}