// ===== input =====

input[type='text'],
input[type='email'],
input[type='tel'],
textarea {
  -webkit-appearance: none;
  appearance: none;
}

textarea {
  resize: none;
  padding: 10px 10px;
}

// сброс при автозаполнении

input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 70px #fff inset;
  -webkit-text-fill-color: #B8B9D0;
}


// === sending ===

.form {
  position: relative;
}


.form__item {
  position: relative;
  margin-bottom: 15px;
}


// === input ===

input::placeholder,
textarea::placeholder {
  text-transform: uppercase;
  font-weight: 500;
  opacity: 1;
  text-overflow: ellipsis;
}



.form__input {
  display: block;
  width: 100%;
  height: rem(50);
  padding: 0px 20px;
  font-size: rem(14);
  letter-spacing: 0.1em;
  border-bottom: 2px solid #8491A5;
  transition: .1s ease 0s;

  &--page {
    color: #262F3C;
    background-color: #fff;

    &::placeholder {
      color: #8491A5;
    }

    &:focus {
      color: #3D414C;
      border-color: $color-1;
    }

    @media (pointer: fine) {
      &:hover {
        border-color: $color-1;
      }
    }
  }
}


.form__input-textarea {
  height: rem(120);
  padding: 16px 20px;
}

