// ===== title =====



.title-1 {
  $title-1: 76;
  line-height: 76/$title-1;
  // margin-bottom: 40/$title-1 * 1em;
  @include adaptiv("font-size", $title-1, 46, 1);
  color: #fff;
  font-weight: 400;
}

.title-2 {
  $title-2: 46;
  line-height: 1.2;
  margin-bottom: 40/$title-2 * 1em;
  @include adaptiv("font-size", $title-2, 36, 1);
  font-weight: 400;
}

.title-3 {
  $title-3: 26;
  line-height: 1.4;
  margin-bottom: 30/$title-3 * 1em;

  font-size: rem(20);

  @media (min-width: $md) {
    font-size: rem($title-3);
  }

}


.suptitle {
  margin-bottom: 5px;
  color: $color-hover-1;
  font-weight: bold;
  font-size: rem(12);
  line-height: 180%;
  letter-spacing: 0.05em;
  text-transform: uppercase;

  @media (min-width: $md) {
    margin-bottom: 10px;
    font-size: rem(14);
  }
}