
// icon plus

.i-plus::before,
.i-plus::after {
  content: '';
  position: absolute;
  right: 15px;
  top: 24px;
  display: block;
  width: 12px;
  height: 2px;
  background-color: $color-1;
  transition: 0.2s ease 0s;

  @media (min-width: $md) {
    top: 26px;
    right: 30px;
  }
  @media (min-width: $xl) {
     display: none;
  }
}

.i-plus:after {
  transform: rotate(90deg);
}

.i-plus.active:after {
  transform: rotate(0deg);
}


.i-down::after {
  content: '';
  display: inline-block;
  margin-left: 6px;
  transform: translateY(3px);
  border: 5px solid transparent;
  border-top-color: inherit;
}


// svg sprite

.icon {
	display: inline-block;
	width: 36px;
	height: 36px;
	stroke-width: 0;
	stroke: currentColor;
	fill: currentColor;
}



[class^="icon-"]::before,
 [class*=" icon-"]::before {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  vertical-align: middle;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

