// ==== General ==== 

html {
	height: 100%;
	width: 100%;
	font-size: 100%;
	-webkit-text-size-adjust: 100%;
	-ms-overflow-style: scrollbar;
}

body {
	height: 100%;
	width: 100%;
	font-family: 'TREBUC', sans-serif;
	font-size: rem(15);
  font-weight: 400;
	line-height: 1.6;
	color: #262F3C;

	@media (min-width: $md) {
		font-size: rem(16);
	}
}

body.lock {
	overflow: hidden;
}


.wrapper {
	display: flex;
	flex-direction: column;
	width: 100%;
	min-height: 100%;
	background-color: #F4F5F9;

	@media (min-width: $md) {
	}
}


.main {
	flex: 1 0 auto;
}

.footer {
	flex: 0 0 auto;
}


._cover,
._contain {
	position: relative;
}

._cover img,
._contain img {
	position: absolute;
	height: 100%;
	width: 100%;
}

._cover img {
	object-fit: cover;
}

._contain img {
	object-fit: contain;
}

.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	padding: 0;
	border: 0;
	overflow: hidden;
	clip: rect(0 0 0 0);
}

.hidden {
	display: none !important;
}



p {
	margin-bottom: rem(10);
}