// ===== footer ===== 

.footer {
	padding: 20px 0;
	color: $color-gray;
	text-align: center;
	background-color: #fff;

	@media (min-width: $md) {
		padding: 40px 0;
	}
}

.footer-page {
	margin-bottom: 70px;

	@media (min-width: $md) {
		margin-bottom: 0;
	}
}

.copy {
	margin-bottom: 10px;

	@media (min-width: $md) {
		margin-bottom: 4px;
	}
}